.input-container {
    display: flex;
    align-items: flex-start;
}

.ready-button {
    background-color: #3a85fc;
    font-family: Arial, Helvetica, sans-serif;
    color: #dedede;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 25px;
    padding-right: 25px;
    margin-left: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition-property: background-color;
    transition-duration: 300ms;
    outline: none;
    border: none;
}
.ready-button:hover {
    background-color: #3974d4;
}

.input-box-big {
    background-color:#303030;
    color: #dedede;
    border: #dedede solid 1px;
    border-radius: 5px;
    resize: none;
    height: 8em;
    width: 20em;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
}
